

 .map {
  position: absolute;
  width: 90px;
  /* background-color: brown; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  
  grid-column-gap: 0px;
  
  grid-gap: 0px;
  padding: 150px 0px 0px 270px;
  margin: 0px;
}

.mapbreake {
 
  border: 8px solid rgb(28, 25, 25);
  width: 330px;
  height: 450px;
  border-radius: 20px;
  margin: 5px 10px; 
}


.Nodata{
   font-size: 50px; color:green ; transform: translate(800px,200px);
}













































































